<!--
  属性
    dataList:要渲染的数据
-->
<template>
  <div class="listView">
    <div class="item" v-for="(item,index) in dataList">
      <div class="item-top">
        <img class="img" :src="item.attributes.headurl" alt="">
        <span class="nickname">{{item.attributes.nickname}}</span>
        <span class="date">{{item.createdAt.getFullYear()+"-"
          +(item.createdAt.getMonth()+1)+"-"
          +item.createdAt.getDate()+" "
          +item.createdAt.getHours()+":"
          +item.createdAt.getMinutes()}}</span>
      </div>
      <h3 class="item-title">
        <router-link class="item-title_a" :to="{path:'/home/detail/',query:{id:item.id}}">{{item.attributes.title}}
        </router-link>
      </h3>
      <div class="content" v-html="item.attributes.content"></div>
    </div>

  </div>
</template>

<script>
  export default {
    props: {
      dataList: {
        type: Array,
        default: []
      }
    },
    data() {
      return {}
    }
  }
</script>

<style scoped>
  .item-title_a {
    color: #333;
    text-decoration: none;
  }

  .item-title:hover {
    text-decoration: underline;
  }

  .item {
    /*width: 670px;*/
    text-align: left;
    margin-top: 10px;
    margin-bottom: 30px;
    padding-bottom: 16px;
    border-bottom: 1px solid #ccc;
  }

  .item-top {
    display: flex;
    align-items: center;
  }

  .item-title {
    padding: 16px 0;
    font-size: 24px;
  }

  .img {
    width: 40px;
    height: 40px;
    border-radius: 100%;
  }

  .nickname {
    margin-left: 16px;
    font-size: 16px;
  }

  .date {
    margin-left: 12px;
    font-size: 14px;
    color: #949494;
  }

  .content {
    /*width: 670px;*/
    height: 60px;
    line-height: 20px;
    font-size: 16px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
  }
</style>



