<template>
  <div class="publish">
    <div class="publish-top">
      <h2>标题</h2>
      <textarea v-model="article_title" placeholder="请输入标题" class="article-title"></textarea>
    </div>
    <div class="toolbar">
      <ul>
        <li>图片</li>
        <li @click="re_write">重做</li>
      </ul>
      <h3>{{size}}</h3>
      <ul>
        <li>保存</li>
        <li @click="publish">发表文章</li>
      </ul>
    </div>
    <div class="editor">
      <textarea class="article-content" :value="input" @input="update"></textarea>
      <div v-html="compiledMarkdown"></div>
    </div>
    <div class="wrapper-toast">
      <toast :is-show-toast="isShowToast" :text="toast_text"></toast>
    </div>
  </div>
</template>

<script>
  import marked from 'marked'
  import _ from 'lodash'
  import Toast from 'base/toast/Toast'
  import AV from 'leancloud-storage'
  import {toPublish} from "../../common/js/publish";

  export default {
    data() {
      return {
        input: '',
        size: 0,
        toast_text: '',
        isShowToast: false,
        article_title: ''
      }
    },
    mounted() {
      eventBus.$emit('is100', true)
    },
    components: {
      Toast
    },
    computed: {
      compiledMarkdown() {
        return marked(this.input, {sanitize: true})
      }
    },
    methods: {
      re_write() {
        this.input = ''
        this.article_title = ''
        this.size = 0
      },
      update: _.debounce(function (e) {
        this.input = e.target.value
        this.size = e.target.value.length
      }, 300),
      publish() {
        if (this.article_title === '' || typeof this.article_title === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '标题不能为空';
          return;
        }
        if (this.input === '' || typeof this.input === 'undefined') {
          this.isShowToast = true;
          this.toast_text = '文章不能为空';
          return;
        }
        this.isShowToast = false;
        toPublish(this.article_title, this.compiledMarkdown, (result) => {
          alert('发布成功');
        }, (err) => {
          alert('发布失败');
        })

      }
    }
  }
</script>

<style scoped>
  .publish-top {
    display: flex;
    align-items: center;
  }

  .publish-top h2 {
    padding: 0 30px;
    width: 200px;
  }

  .article-title {
    width: 100%;
    height: 60px;
    font-size: 24px;
    line-height: 60px;
    padding: 0 10px;
    font-weight: bold;
    border: none;
    background: #eeeeee;
  }

  .editor {
    margin: 0;
    height: 100%;
    font-family: 'Helvetica Neue', Arial, sans-serif;
    color: #333;
  }

  .article-content, .editor div {
    display: inline-block;
    width: 49%;
    height: 100%;
    vertical-align: top;
    box-sizing: border-box;
    padding: 0 20px;
    background: #fcfaf2;
    overflow: auto;
    text-align: left;
  }

  .article-content {
    border: none;
    border-right: 1px solid #ffffff;
    resize: none;
    outline: none;
    background-color: #f6f6f6;
    font-size: 14px;
    font-family: 'Monaco', courier, monospace;
    padding: 20px;
  }

  .publish {
    height: 100%;
  }

  .toolbar {
    background: #d9d9d9;
    height: 40px;
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    padding: 0 20px;
  }

  .toolbar ul {
    display: flex;
  }

  .toolbar ul li {
    cursor: pointer;
    margin: 0 10px;
  }

  .wrapper-toast {
    position: absolute;
    width: 100%;
    right: 0;
    bottom: 100px;
  }
</style>



